export interface Note {
  id: string
  title: string
  subtitle: string
  content: string
  createdAt: string
  updatedAt: string
  folderId: string
}

export interface Folder {
  id: string
  name: string
  createdAt: string
  updatedAt: string
}

export interface NotesData {
  folders: Folder[]
  notes: Note[]
}

const STORAGE_KEY = 'notesapp_data'

export const saveData = (data: NotesData): void => {
  try {
    localStorage.setItem(STORAGE_KEY, JSON.stringify(data))
  } catch (error) {
    console.error('Erro ao salvar dados:', error)
    throw new Error('Falha ao salvar dados')
  }
}

export const loadData = (): NotesData => {
  try {
    const stored = localStorage.getItem(STORAGE_KEY)
    if (!stored) {
      // Dados iniciais padr√£o
      const defaultData: NotesData = {
        folders: [
          {
            id: 'default-folder',
            name: 'Minhas Anota√ß√µes',
            createdAt: new Date().toISOString(),
            updatedAt: new Date().toISOString()
          }
        ],
        notes: [
          {
            id: 'welcome-note',
            title: 'Bem-vindo ao NotesApp!',
            subtitle: 'Sua primeira anota√ß√£o',
            content: 'Este √© um exemplo de anota√ß√£o. Voc√™ pode editar este texto, criar novas anota√ß√µes e organizar tudo em pastas.\n\nRecursos dispon√≠veis:\n‚Ä¢ Criar e organizar pastas\n‚Ä¢ Adicionar, editar e excluir anota√ß√µes\n‚Ä¢ Buscar por conte√∫do\n‚Ä¢ Modo escuro\n‚Ä¢ Design responsivo',
            createdAt: new Date().toISOString(),
            updatedAt: new Date().toISOString(),
            folderId: 'default-folder'
          }
        ]
      }
      saveData(defaultData)
      return defaultData
    }
    return JSON.parse(stored)
  } catch (error) {
    console.error('Erro ao carregar dados:', error)
    // Retornar dados padr√£o em caso de erro
    return {
      folders: [],
      notes: []
    }
  }
}

export const deleteData = (): void => {
  try {
    localStorage.removeItem(STORAGE_KEY)
  } catch (error) {
    console.error('Erro ao deletar dados:', error)
    throw new Error('Falha ao deletar dados')
  }
}

// Fun√ß√µes auxiliares para gerar IDs √∫nicos
export const generateId = (): string => {
  return Date.now().toString(36) + Math.random().toString(36).substr(2)
}

// Fun√ß√µes espec√≠ficas para manipular pastas
export const createFolder = (name: string): Folder => {
  return {
    id: generateId(),
    name,
    createdAt: new Date().toISOString(),
    updatedAt: new Date().toISOString()
  }
}

// Fun√ß√µes espec√≠ficas para manipular notas
export const createNote = (folderId: string, title: string = 'Nova Anota√ß√£o'): Note => {
  return {
    id: generateId(),
    title,
    subtitle: '',
    content: '',
    createdAt: new Date().toISOString(),
    updatedAt: new Date().toISOString(),
    folderId
  }
}

export const updateNote = (note: Note, updates: Partial<Omit<Note, 'id' | 'createdAt'>>): Note => {
  return {
    ...note,
    ...updates,
    updatedAt: new Date().toISOString()
  }
}
